section.categorias_contenedor{
    background-color: var(--negro);
    padding: 20px;
}
.categorias__titulo{
    width: fit-content;
    height: fit-content;
    padding: 12px;
    text-align: center;
    background-color: var(--negro);
    color: var(--blanco);
    border-radius: 4px;
    margin: 20px;
}

.input-color{
    position: absolute;
    top: 20px;
    right: 50px;
}

.equipo h3{
    font-weight: 400;
    color: #212121;
    border-bottom: 4px solid #57C278;
    display: inline-block;
    padding-bottom: 8px;
}

.videos{
    display: flex;
    justify-content: space-evenly;
    flex-wrap: wrap;
    margin: auto;
}