section.banner{
    display: flex;
    padding: 20px;
    background-image: url('./logo.jpg');
    justify-content: space-between;
    color: var(--blanco);
    height: fit-content;
    flex-wrap: wrap;
}
.banner__desc{
    max-width: 500px;
    margin-top: 250px;
    line-height: 35px;
}
.banner__desc h1{
    font-size: 60px;
    font-weight: 400;
    color: var(--gris);
    background-color: var(--celeste);
    padding: 15px;
    text-align: center;
    width: fit-content;
    margin-bottom: 30px;
    border-radius: 10px;
    line-height: 70.31px;
}
.exp{
    width: fit-content;
    height: fit-content;
    padding: 12px;
    text-align: center;
    border: var(--celeste) solid;
    background-color: var(--celeste);
    color: var(--blanco);
    border-radius: 4px;
    margin: 20px;
}
.exp_video{
    margin-top: 250px;
    border: var(--celeste) solid ;
}